@charset "UTF-8";

:root {
  --yellow: #E1FD2C;
  --yellow-hover: #9acd4f;
  --yellow-lite: #d4f405;
  --green: #16774A;
  --pink: #F6A0A9;
  --gray: #BBD4D0;
  --violet: #4F2479;
  --white: white;
}

* {
  box-sizing: border-box;
}

*,
::after,
::before {
  box-sizing: border-box;
}

body {
  margin: 0;
}

:root {
  --index: calc(1vw + 1vh);
  --vw: calc(100vw/1440);
  --container-padding-x: 26px;
  --container-width: min(1440px, max(var(--vw)* 1440, 24px));
}

a,
a:link,
a:visited {
  text-decoration: none;
}

* {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 36px;
  letter-spacing: 0;
}

::-webkit-scrollbar {
  width: 0px;
  height: 5px;
  background: transparent;
}

/* Optional: show position indicator in red */

::-webkit-scrollbar-thumb {
  background: var(--grey40);
}

.reset {
  margin: 0;
  padding: 0;
}

.container {
  max-width: calc(var(--container-width) + var(--container-padding-x) * 2);
  margin: 0 auto;
  padding-left: var(--container-padding-x);
  padding-right: var(--container-padding-x);
  width: 100%;
}

.fadeOut {
  opacity: 0;
}

.fadeIn {
  opacity: 1;
}

.input-reset {
  background: 0 0;
  margin: 0;
  border: none;
  padding: 0;
}

.button-reset {
  background: 0 0;
  margin: 0;
  border: none;
  padding: 0;
  color: inherit;
  cursor: pointer;
}

.btn {
  display: inline-block;
  padding: 19px 29px;
  border: 1px solid var(--violet);
  background-color: var(--yellow);
  border-radius: 4px;
  cursor: pointer;
  color: var(--violet);
  text-align: center;
  vertical-align: middle;
  transition: all 0.2s;
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
  letter-spacing: 0;
  text-transform: uppercase;
}

.btn:hover {
  background-color: var(--yellow-hover);
  border: 1px solid var(--yellow-hover);
}

.btn-area {
  position: absolute;
  background: transparent;
  border: transparent;
  margin: 0;
  padding: 0;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.carousel {
  display: block;
  margin: 0 auto;
  position: relative;
}

.carousel__indicators {
  position: relative;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 15;
  display: flex;
  justify-content: center;
  margin-right: 15%;
  margin-left: 15%;
  list-style: none;
}

.carousel__item {
  box-sizing: content-box;
  flex: 0 1 auto;
  width: 40px;
  height: 4px;
  margin-right: 3px;
  margin-left: 3px;
  cursor: pointer;
  background-color: #CFD3DA;
  background-clip: padding-box;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  opacity: 0.3;
  transition: opacity 0.6s ease;
}

.carousel__active {
  opacity: 1;
}

/* Прописная (заглавная)  первая буква  */

.text-capitalize {
  text-transform: capitalize;
}

/* Прописные (заглавные)  все  */

.text-uppercase {
  text-transform: uppercase;
}

/* Строчные (маленькие) все  */

.text-lowercase {
  text-transform: lowercase;
}

/* переносить или нет длинные слова */

.text-break-word {
  word-wrap: break-word;
  -webkit-text-size-adjust: 100%;
}

/* перенос слов на новую строку */

.text-break-all {
  word-break: break-all;
}

/* НЕ перенос слов на новую строку */

.text-nobreak {
  white-space: nowrap;
}

.wrapper {
  background-color: var(--green);
  overflow-x: hidden;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: polygon(0px 0px, 0px 0px, 0px 0px, 0px 0px);
}

.attention {
  font-weight: 400;
  font-size: 12px;
  line-height: 20.4px;
  letter-spacing: 0.5px;
  text-align: center;
  text-transform: uppercase;
  background-color: var(--attention);
  color: var(--text-attention);
  padding: 10px;
  text-align: center;
}

.face {
  display: flex;
  margin: 0 0 0 auto;
  width: min(450px, max(var(--vw) * 450, 100px));
  height: min(492px, max(var(--vw) * 492, 109px));
  transform: rotateZ(-16deg);
  z-index: 10;
  position: absolute;
  right: 30px;
  top: calc(-1 * min(263px, max(var(--vw) * 263, 10px)));
}

.face__img {
  border-radius: min(49px, max(var(--vw) * 49, 16px));
  max-width: 100%;
  height: auto;
}

.zoom-hover {
  transform: scale(1);
  transition: transform 0.3s, fill 0.3s;
  cursor: pointer;
}

.zoom-hover:hover {
  fill: var(--primary);
  transform: scale(1.2);
}

.header {
  background-color: var(--green);
}

.header__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  background-color: var(--green);
  padding: 32px 0 12px 20px;
}

.header__logo {
  height: min(48px, max(var(--vw) * 48, 34px));
}

.header__letter {
  width: min(45px, max(var(--vw) * 45, 20px));
  height: min(48px, max(var(--vw) * 48, 22px));
}

.header__btn {
  background-color: var(--yellow);
  text-align: center;
}

.header__btn:hover {
  will-change: transform;
  background-color: var(--yellow-hover);
  transition: background-color 0.5s;
}

.header__link {
  padding: 8px 27px;
  padding: 8px min(27px, max(var(--vw) * 27, 0px));
  color: var(--green);
  font-weight: 400;
  font-size: 18px;
  line-height: 0;
}

.header__link:hover {
  will-change: transform;
  color: var(--violet);
  transition: color 0.2s;
}

.header-content {
  background-color: var(--green);
  padding-bottom: 78px;
}

.header-content__wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  padding-top: 65px;
  --orbit-x: min(981px, max(var(--vw)* 981, 360px));
  --orbit-y: min(497px, max(var(--vw)* 497, 182px));
}

.header-content__block {
  margin: 0 min(92px, max(var(--vw) * 92, 0px));
  position: absolute;
  top: min(156px, max(var(--vw) * 156, 34px));
  z-index: 20;
}

.header-content__title {
  font-weight: 400;
  font-size: min(105px, max(var(--vw) * 105, 16px));
  line-height: min(108.8px, max(var(--vw) * 108.8, 20px));
  letter-spacing: 0;
  text-align: center;
  text-transform: uppercase;
  color: var(--yellow);
}

.header-content__oval {
  width: min(981px, max(var(--vw) * 981, 360px));
  height: min(497px, max(var(--vw) * 497, 182px));
  background-color: transparent;
  border: 1px solid var(--yellow);
  border-radius: 50%;
  transform: rotateZ(-21deg);
  position: relative;
  margin: 0 auto;
  z-index: 10;
}

.header-content__pill {
  position: absolute;
  width: min(167px, max(var(--vw) * 167, 63px));
  height: min(112px, max(var(--vw) * 112, 42px));
}

.header-content__pill_1 {
  transform: rotateZ(19deg);
  --orbit-x: min(1064px, max(var(--vw)* 1064, 391px));
  --orbit-y: min(553px, max(var(--vw)* 553, 203px));
  --angle: 214deg;
  top: min(179px, max(var(--vw) * 179, 0px));
  left: min(406px, max(var(--vw) * 406, 0px));
  --x-amplitude: calc(var(--orbit-x ) / 2);
  --y-amplitude: calc(var(--orbit-y ) / 2);
  --x: calc(cos(var(--angle)) * var(--x-amplitude));
  --y: calc(sin(var(--angle)) * var(--y-amplitude));
  translate: var(--x) var(--y);
  animation: revolve 6000ms linear infinite;
}

@supports (-webkit-touch-callout: default) {
  .header-content__pill_1 {
    top: min(22px, max(var(--vw) * 22, 8.4px));
    left: calc(-1 * min(38px, max(var(--vw) * 38, 14.4px)));
  }
}

.header-content__pill_2 {
  transform: rotateZ(352deg);
  width: min(141px, max(var(--vw) * 141, 63px));
  height: min(96px, max(var(--vw) * 96, 42px));
  --orbit-x: min(981px, max(var(--vw)* 981, 360px));
  --orbit-y: min(497px, max(var(--vw)* 497, 182px));
  --angle: 320deg;
  top: min(198px, max(var(--vw) * 198, 0px));
  left: min(386px, max(var(--vw) * 386, 0px));
  --x-amplitude: calc(var(--orbit-x ) / 2);
  --y-amplitude: calc(var(--orbit-y ) / 2);
  --x: calc(cos(var(--angle)) * var(--x-amplitude));
  --y: calc(sin(var(--angle)) * var(--y-amplitude));
  translate: var(--x) var(--y);
  animation: revolve2 5000ms linear infinite;
}

@supports (-webkit-touch-callout: default) {
  .header-content__pill_2 {
    top: min(40px, max(var(--vw) * 40, 15.2px));
    left: min(757px, max(var(--vw) * 757, 287.6px));
  }
}

.header-content__pill_3 {
  transform: rotateZ(65deg);
  --orbit-x: min(1064px, max(var(--vw)* 1064, 360px));
  --orbit-y: min(619px, max(var(--vw)* 619, 182px));
  --angle: 49deg;
  top: min(198px, max(var(--vw) * 198, 0px));
  left: min(386px, max(var(--vw) * 386, 0px));
  --x-amplitude: calc(var(--orbit-x ) / 2);
  --y-amplitude: calc(var(--orbit-y ) / 2);
  --x: calc(cos(var(--angle)) * var(--x-amplitude));
  --y: calc(sin(var(--angle)) * var(--y-amplitude));
  translate: var(--x) var(--y);
  animation: revolve3 5000ms linear infinite;
}

@supports (-webkit-touch-callout: default) {
  .header-content__pill_3 {
    top: min(466px, max(var(--vw) * 466, 177px));
    left: min(741px, max(var(--vw) * 741, 281.5px));
  }
}

.header-content__text {
  position: absolute;
  font-weight: 400;
  font-size: min(24px, max(var(--vw) * 24, 12px));
  line-height: 30px;
  line-height: min(30px, max(var(--vw) * 30, 18px));
  letter-spacing: 0;
  text-align: center;
  color: var(--yellow);
  position: relative;
  max-width: 384px;
  margin-top: 10px;
  z-index: 40;
}

@property --angle {
  syntax: "<angle>";
  inherits: true;
  initial-value: 0deg;
}

@keyframes revolve {
  from {
    --angle: -146deg;
  }

  to {
    --angle: 214deg;
  }
}

@keyframes revolve2 {
  from {
    --angle: -40deg;
  }

  to {
    --angle: 320deg;
  }
}

@keyframes revolve3 {
  from {
    --angle: 51deg;
  }

  to {
    --angle: 411deg;
  }
}

.prime {
  background-color: var(--pink);
  position: relative;
  --angle: 18deg;
  padding-top: min(417px, max(var(--vw) * 417, 70px));
  padding-bottom: min(132px, max(var(--vw) * 132, 50px));
  padding-left: 18px;
}

.prime__wrap {
  position: relative;
  margin-bottom: 141px;
  margin-bottom: min(141px, max(var(--vw) * 141, 70px));
}

.prime__img {
  max-width: 100%;
  height: auto;
}

.prime__pills {
  position: absolute;
  top: 0;
  left: 0;
  max-width: 843px;
  width: 100%;
  height: 100%;
}

.prime__pill-1 {
  position: absolute;
  width: min(150px, max(var(--vw) * 150, 50px));
  height: min(104px, max(var(--vw) * 104, 34px));
  left: min(95px, max(var(--vw) * 95, 0px));
  top: 63px;
  transform: rotateZ(45deg);
  animation: rotation 2000ms linear infinite;
}

.prime__pill-2 {
  position: absolute;
  width: min(245px, max(var(--vw) * 245, 50px));
  height: min(149px, max(var(--vw) * 149, 50px));
  top: 7px;
  right: -86px;
  right: calc(-1 * min(86px, max(var(--vw) * 86, 0px)));
  transform: rotateZ(359deg);
  animation: rotation 2000ms linear infinite;
}

.prime__pill-3 {
  position: absolute;
  width: min(161px, max(var(--vw) * 161, 50px));
  height: min(114px, max(var(--vw) * 114, 50px));
  top: 74%;
  right: 127px;
  transform: rotateZ(329deg);
  animation: rotation 2000ms linear infinite;
}

.prime__picture {
  position: relative;
  display: flex;
}

.prime__stars {
  display: flex;
  flex-wrap: wrap;
  width: min(120px, max(var(--vw) * 120, 50px));
  position: absolute;
  bottom: calc(-1 * min(53px, max(var(--vw) * 53, 10px)));
  column-gap: min(20px, max(var(--vw) * 20, 10px));
  row-gap: min(20px, max(var(--vw) * 20, 10px));
  margin-left: min(70px, max(var(--vw) * 70, 10px));
}

.prime__title {
  font-weight: 700;
  font-size: min(40px, max(var(--vw) * 40, 26px));
  line-height: min(70px, max(var(--vw) * 70, 36px));
  letter-spacing: 0;
  color: var(--violet);
  margin: 0 min(461px, max(var(--vw) * 461, 50px)) 128px 70px;
}

.prime__text {
  font-weight: 700;
  font-size: 20px;
  line-height: min(45px, max(var(--vw) * 45, 30px));
  letter-spacing: 0;
  color: var(--violet);
  margin: 0 190px 79px 675px;
}

.prime__btn {
  margin: 0 190px 137px 675px;
}

.prime__poster {
  display: flex;
  justify-content: center;
}

.prime__video {
  display: flex;
  justify-content: center;
  align-items: center;
}

.prime__play {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid var(--yellow);
  border-radius: 50%;
  height: min(320px, max(var(--vw) * 320, 24px));
  width: min(320px, max(var(--vw) * 320, 24px));
  background-color: transparent;
  position: absolute;
  cursor: pointer;
  z-index: 100;
  transition: transform 0.75s cubic-bezier(0.075, 0.5, 0, 1);
  will-change: auto;
  transform: translate(0px, 0px);
}

.prime__play-btn {
  position: absolute;
  background-color: transparent;
  width: min(110px, max(var(--vw) * 110, 10px));
  height: min(110px, max(var(--vw) * 110, 10px));
  border: none;
}

.star {
  position: relative;
  width: min(45px, max(var(--vw) * 45, 20px));
  height: min(45px, max(var(--vw) * 45, 20px));
  fill: var(--yellow);
  stroke: var(--yellow);
  transform: rotateZ(32deg);
  stroke-width: 1px;
}

.star_active {
  animation: fill-color 2000ms linear infinite;
}

.star_white {
  width: min(58px, max(var(--vw) * 58, 20px));
  height: min(58px, max(var(--vw) * 58, 20px));
  fill: var(--white);
  stroke: var(--green);
  transform: rotateZ(0deg);
  stroke-width: 3px;
}

.star_green {
  width: min(58px, max(var(--vw) * 58, 20px));
  height: min(58px, max(var(--vw) * 58, 20px));
  fill: var(--green);
  stroke: var(--green);
  transform: rotateZ(64deg);
  stroke-width: 3px;
}

@keyframes fill-color {
  from {
    fill: var(--yellow);
  }

  33% {
    fill: var(--yellow-hover);
  }

  66% {
    fill: white;
  }

  to {
    fill: var(--yellow);
  }
}

@keyframes rotation {
  from {
    transform: rotateY(0deg);
  }

  50% {
    transform: rotateY(360deg);
  }

  to {
    transform: rotateY(0deg);
  }
}

.play {
  width: 100%;
  height: 100%;
}

.questions {
  background-color: var(--yellow);
  padding-top: min(73px, max(var(--vw) * 73, 50px));
  position: relative;
  padding-bottom: min(100px, max(var(--vw) * 100, 30px));
}

.questions__title {
  font-weight: 300;
  font-size: min(159px, max(var(--vw) * 159, 70px));
  line-height: min(155px, max(var(--vw) * 155, 70px));
  color: var(--violet);
  text-align: center;
  padding: 16px 0 20px;
  border-top: 1px solid var(--violet);
  border-bottom: 1px solid var(--violet);
  margin-bottom: 21.5px;
}

.questions__faq {
  margin-bottom: min(152px, max(var(--vw) * 152, 20px));
}

.questions__details {
  position: relative;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  transition: 0.3s;
  overflow: hidden;
  padding: min(55px, max(var(--vw) * 55, 10px)) min(112px, max(var(--vw) * 112, 40px));
  border-top: 1px solid var(--violet);
  border-bottom: 1px solid var(--violet);
}

.questions__details::after {
  position: absolute;
  font-size: min(112px, max(var(--vw) * 112, 40px));
  font-weight: 100;
  color: var(--violet);
  right: min(111px, max(var(--vw) * 111, 10px));
  content: "+";
  display: block;
  transform-origin: center;
  transition: 0.3s;
}

.questions__details[open]::after {
  transform: rotate(45deg);
}

.questions__details[open] {
  padding-bottom: 100px;
}

.questions__details[open] > summary {
  margin-bottom: min(32px, max(var(--vw) * 32, 18px));
}

.questions__faq-title {
  font-weight: 700;
  font-size: min(48px, max(var(--vw) * 48, 18px));
  line-height: min(48px, max(var(--vw) * 48, 24px));
  padding: 12px 0;
  cursor: pointer;
  list-style: none;
  transition: 0.3s;
  color: var(--violet);
}

.questions__faq-title::-webkit-details-marker {
  display: none;
}

.questions__content {
  padding: 0 min(32px, max(var(--vw) * 32, 10px));
  margin-bottom: -60px;
}

.questions__inner {
  font-size: min(18px, max(var(--vw) * 18, 14px));
  line-height: min(36px, max(var(--vw) * 36, 20px));
}

.box {
  padding: min(140px, max(var(--vw) * 140, 20px)) 0;
  position: absolute;
  width: 105%;
  width: 100%;
  overflow: hidden;
  margin: 0 auto;
  border-bottom: 2px solid var(--yellow);
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
}

.stripe {
  display: flex;
  justify-content: space-between;
  background-color: var(--violet);
  padding: min(20px, max(var(--vw) * 20, 10px)) min(50px, max(var(--vw) * 50, 10px));
  position: absolute;
  width: 105%;
  border: 3px solid var(--yellow);
}

.stripe_one {
  transform: rotate(-9deg);
  margin-top: min(30px, max(var(--vw) * 30, 12px));
}

.stripe_two {
  transform: rotate(5deg);
}

.stripe__text {
  font-weight: 400;
  font-size: 64px;
  font-size: min(64px, max(var(--vw) * 64, 12px));
  line-height: min(64px, max(var(--vw) * 64, 12px));
  color: var(--yellow);
  margin: 0 min(45px, max(var(--vw) * 45, 0px)) 0 0;
}

.banners {
  background-color: var(--gray);
  position: relative;
  padding-bottom: min(105px, max(var(--vw) * 105, 40px));
}

.banners__box {
  margin: 0 auto;
  width: 100vw;
}

.banners__grid {
  position: absolute;
  display: grid;
  grid-template-columns: repeat(6, max(17vw, 100px));
  grid-template-rows: repeat(5, max(17vw, 100px));
}

.banners__item {
  position: relative;
  border-right: 1px solid var(--green);
  border-bottom: 1px solid var(--green);
  z-index: 10;
}

.banners__item:nth-child(6n) {
  border-right: 0;
}

.banners__item:nth-last-child(-n+6) {
  border-bottom: 0;
}

.banners__line {
  border-bottom: 2px solid var(--yellow);
  top: min(204px, max(var(--vw) * 204, 150px));
  position: relative;
}

.banners__block {
  padding-top: min(290px, max(var(--vw) * 290, 20px));
  position: relative;
}

.banners__wearenoodz {
  font-weight: 500;
  font-size: 19px;
  line-height: 24px;
  padding: min(16px, max(var(--vw) * 16, 10px));
  border: 2px solid var(--violet);
  background-color: var(--yellow);
  margin: 0 auto;
  width: min(214px, max(var(--vw) * 214, 150px));
  z-index: 10;
  position: relative;
  top: 61px;
  left: 11px;
  color: var(--violet);
}

.banners__row {
  display: flex;
  justify-content: center;
}

.banners__picture {
  position: relative;
  border: 3px solid var(--violet);
}

.banners__picture_we {
  transform: rotateZ(-18deg);
  width: min(491px, max(var(--vw) * 491, 50px));
  height: min(515px, max(var(--vw) * 515, 53px));
  left: min(84px, max(var(--vw) * 84, 0px));
}

.banners__picture_are {
  width: min(457px, max(var(--vw) * 457, 50px));
