}

.banners__picture_noodz {
  width: min(491px, max(var(--vw) * 491, 50px));
  height: min(515px, max(var(--vw) * 515, 52px));
  transform: rotateZ(-17deg);
  left: calc(-1 * min(84px, max(var(--vw) * 84, 0px)));
}

.banners__img {
  width: 100%;
  height: auto;
}

.smiley {
  padding: 190px 10px;
  padding: min(190px, max(var(--vw) * 190, 20px)) 10px;
}

.smiley__face {
  display: flex;
  justify-content: center;
  width: 228px;
  height: 230px;
  border-radius: 50%;
  background-color: var(--yellow);
  border: 3px solid var(--green);
  margin: 0 auto;
  position: relative;
}

.smiley__eyes {
  width: 69px;
  height: 44px;
  border-left: 3px solid var(--green);
  border-right: 3px solid var(--green);
  top: 44px;
  left: 50%;
  position: absolute;
  will-change: contents;
  animation: blink 1100ms linear infinite;
}

.smiley__mouth {
  width: 150px;
  height: 14px;
  border: 3px solid var(--green);
  background-color: var(--white);
  top: 124px;
  position: absolute;
  z-index: 10;
}

.smiley__mouth-shadow {
  width: 148px;
  height: 68px;
  border: 3px solid var(--green);
  border-bottom-right-radius: 76px;
  border-bottom-left-radius: 76px;
  background-color: var(--green);
  top: 134px;
  left: 48px;
  position: absolute;
}

.smiley__language {
  width: 83px;
  height: 100px;
  top: 138px;
  left: 80px;
  position: absolute;
  z-index: 20;
}

.smiley__language-shadow {
  width: 83px;
  height: 100px;
  top: 150px;
  left: 67px;
  position: absolute;
  z-index: 10;
}

.smiley__face-star {
  top: 86px;
  left: 160px;
  width: 77px;
  height: 73px;
  position: absolute;
  z-index: 20;
}

.smiley__line {
  width: 0px;
  height: 48px;
  border-left: 3px solid var(--green);
  top: 139px;
  left: 121px;
  position: absolute;
  z-index: 30;
}

.smiley__bubbles-left {
  width: 56px;
  height: 57px;
  position: relative;
  top: 107px;
  left: 8px;
  z-index: 10;
}

.smiley__bubbles-right {
  width: 55px;
  height: 61px;
  position: relative;
  top: 119px;
  left: 102px;
  z-index: 10;
}

.smiley__star {
  top: 130px;
  right: calc(-1 * min(230px, max(var(--vw) * 230, 150px)));
  width: 77px;
  height: 73px;
  position: relative;
  z-index: 20;
}

.smiley__pill-left {
  position: absolute;
  width: 53px;
  height: 44px;
  bottom: 12px;
  left: -37px;
}

.smiley__pill-right {
  position: absolute;
  width: 53px;
  height: 44px;
  bottom: 12px;
  right: -49px;
}

.smiley__toothbrushes {
  display: flex;
  position: relative;
  margin: 0 auto;
  width: 377px;
  top: 63px;
}

.smiley__block-left {
  will-change: transform;
  animation: move1 1000ms linear infinite;
}

.smiley__block-right {
  will-change: transform;
  animation: move2 1100ms linear infinite;
}

.smiley__toothbrush {
  width: 400px;
  height: 30px;
  position: absolute;
  background-color: var(--pink);
  border: 2px solid var(--green);
}

.smiley__toothbrush_left {
  transform: rotateZ(20deg);
  border-bottom-left-radius: 25px;
  border-bottom-right-radius: 25px;
  border-top-right-radius: 25px;
}

.smiley__toothbrush_right {
  transform: rotateZ(-20deg);
  border-top-left-radius: 25px;
  border-bottom-left-radius: 25px;
  border-bottom-right-radius: 25px;
}

.smiley__bristle-left {
  width: 64px;
  height: 64px;
  position: absolute;
  top: -99px;
  left: 17px;
}

.smiley__bristle-right {
  width: 64px;
  height: 64px;
  position: absolute;
  top: -97px;
  left: 319px;
  transform: rotateY(180deg);
}

@keyframes move1 {
  from {
    transform: translate(0px, 0px);
  }

  50% {
    transform: translate(60px, 30px);
  }

  to {
    transform: translate(0px, 0px);
  }
}

@keyframes move2 {
  from {
    transform: translate(0px, 0px);
  }

  50% {
    transform: translate(-60px, 30px);
  }

  to {
    transform: translate(0px, 0px);
  }
}

@keyframes blink {
  from {
    left: 50%;
  }

  49% {
    left: 50%;
  }

  50% {
    left: 25%;
  }

  to {
    left: 25%;
  }
}

.form {
  display: flex;
  flex-direction: column;
  row-gap: 20px;
  max-width: 600px;
  margin: 90px auto 0;
  padding: 20px;
}

.form__label {
  font-size: 97px;
  font-size: min(97px, max(var(--vw) * 97, 36px));
  line-height: 112px;
  line-height: min(112px, max(var(--vw) * 112, 45px));
  color: var(--green);
  text-align: center;
}

.form__field {
  display: flex;
  justify-content: space-between;
  border-bottom: 2px solid var(--green);
}

.form__input {
  padding-bottom: 12px;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  text-transform: uppercase;
  width: 90%;
}

.form__input::placeholder {
  text-transform: uppercase;
  color: var(--green);
}

.form__input:focus {
  outline: 0;
  border-radius: 4px;
}

.form__button {
  display: flex;
  align-items: flex-start;
}

.form__arrow {
  width: 27px;
  height: 24px;
  fill: var(--green);
}

.footer {
  background-color: var(--violet);
  padding-top: min(130px, max(var(--vw) * 130, 40px));
  padding-bottom: min(128px, max(var(--vw) * 128, 40px));
  --container-padding-x: min(115px, max(var(--vw)* 115, 24px));
}

.footer__info {
  display: flex;
  gap: min(420px, max(var(--vw) * 420, 24px));
}

.footer__left {
  width: 50%;
}

.footer__right {
  width: 50%;
}

.footer__title {
  font-size: 42px;
  line-height: 48px;
  color: var(--yellow);
  margin-bottom: 24px;
}

.footer__text {
  color: var(--yellow);
}

.footer__logo {
  display: flex;
  justify-content: space-between;
  margin-top: min(112px, max(var(--vw) * 112, 24px));
  margin-bottom: 32px;
}

.footer__letter {
  width: auto;
  height: min(240px, max(var(--vw) * 240, 24px));
}

.footer__block {
  background-color: var(--yellow);
  width: 320px;
  overflow: hidden;
  margin: 0 0 30px auto;
  padding: 7px;
}

.footer__run-lines {
  mask-image: linear-gradient(to right, hsla(0, 0%, 0%, 0), hsl(0, 0%, 0%) 20%, hsl(0, 0%, 0%) 80%, hsla(0, 0%, 0%, 0));
}

.footer__lines {
  display: flex;
  justify-content: space-between;
  animation: marquee 10s infinite linear;
  min-width: 320px;
}

.footer__line {
  display: flex;
  font-size: 18px;
  line-height: 18px;
  padding: 5px;
  color: var(--violet);
  min-width: 160px;
}

.footer__line::before {
  content: url("/assets/img/asterisk.svg");
  width: 20px;
  height: 20px;
  display: block;
}

.footer__links {
  display: flex;
  column-gap: 25%;
}

.footer__link {
  color: var(--yellow);
}

@keyframes marquee {
  0% {
    transform: translateX(100%);
  }

  100% {
    transform: translateX(-100%);
  }
}

